import React, { useEffect, useState } from "react";
import { useNavigate, useLocation } from "react-router-dom";

import { toast } from "react-hot-toast";
import useUserStore from "../../store/useUserStore";
import storageUtils from "../../utils/storageUtils";

import Button from "../../components/Button";
import AppBar from "../../components/AppBar";
import TextInput from "../../components/TextInput";
import { showCustomToast } from "../../components/CustomToast";

import { EyeIcon, EyeSlashIcon } from "@heroicons/react/24/outline";

import { AppDispatch, RootState } from "../../store";
import { useDispatch, useSelector } from "react-redux";
import { useTranslation } from "react-i18next";
import * as userInfoSlice from "../../store/userInfoSlice";
import BottomTab from "../../components/BottomTab";
import { getFontClass } from "../../utils/helper";

const Login: React.FC = () => {
  const {t} = useTranslation();
  const navigate = useNavigate();
  const location = useLocation();
  const params = new URLSearchParams(location.search);
  const route = params.get("route");

  const [logging, setLogging] = useState<boolean>(false);
  const [loginValue, setLoginValue] = useState({ username: "", password: "" });
  const [showPassword, setShowPassword] = useState(false);
  const [errors, setErrors] = useState({
    username: "",
    password: "",
  });
  const [value, setValue] = useState(0);
  useEffect(() => {
    if (route?.includes("/Member")) {
      setValue(4);
    } else if (route?.includes("/Favorite")) {
      setValue(3);
    } else {
      setValue(0); // 預設值
    }
  }, [route]);
  const language = useSelector((State: RootState) => State.init.language);
  const dispatch: AppDispatch = useDispatch();

  const validateInputs = () => {
    let isValid = true;
    const newErrors = {
      username: "",
      password: "",
    };

    // 帳號驗證：6-10個英文數字
    if (loginValue.password !== loginValue.password) {
      newErrors.password = t("密碼錯誤，請重新輸入");
      isValid = false;
    }

    // 密碼驗證：4-10個英文數字
    if (loginValue.username !== loginValue.username ) {
      newErrors.username = t("查無此帳號請重新輸入");
      isValid = false;
    }

    setErrors(newErrors);
    return isValid;
  };

  const handleLogin = () => {
    const { username, password } = loginValue;
    const loginPromise = new Promise((resolve, reject) => {
      setLogging(true);
      setTimeout(async () => {
        const userData = { username: username, password: password }; // 模擬登入
        if (validateInputs()) {
          const result = await dispatch(userInfoSlice.login(userData));
          resolve({ user: result.payload, msg: t("登入成功!") });
        } else {
          reject({ msg: t("請確認帳號或密碼") });
        }
      }, 1000);
    });

    const loadingToastId = showCustomToast(t("登入中..."), "loading");

    loginPromise
    .then((result: any) => {
      const user = result.user;
      if (user && typeof user === "object") {
        showCustomToast(result.msg, "success");
        route === null ? navigate(-1) : navigate(route);
      } else {
        throw new Error("Invalid login session");
      }
      return result;
    })
    .catch((error) => {
      console.log("Promise 被拒絕:", error.msg);
      if (error.msg) {
        showCustomToast(error.msg, "error");
      } else {
        showCustomToast(t('帳號密碼有誤'), "error");
      }
    }).finally(() => {
      toast.dismiss(loadingToastId); // Dismiss the loading toast
      setLogging(false);
    });
  };

  const handleSubmit = (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    handleLogin();
  };

  function eyeIcon() {
    return showPassword ? (
      <EyeIcon
        className="w-6 h-6 text-gray-600 stroke-1.5"
        onClick={() => setShowPassword(!showPassword)}
      />
    ) : (
      <EyeSlashIcon
        className="w-6 h-6 text-gray-600 stroke-1.5"
        onClick={() => setShowPassword(!showPassword)}
      />
    );
  }

  const shouldHideBackBtn =
    route?.includes("/Member") || route?.includes("/Favorite");
  return (
    <div>
      <AppBar
        type={shouldHideBackBtn ? "hideBackBtn" : undefined}
        title={t("登入")}
      />
      <div className="p-4 space-y-4">
        <form className="space-y-4" onSubmit={handleSubmit}>
          <div className={`${getFontClass(language)}`}>
            <label className="block mb-2">{t('帳號')}</label>
            <TextInput
              disabled={logging}
              placeholder={t("請輸入帳號")}
              value={loginValue.username}
              onInputChange={(value: any) =>
                setLoginValue({ ...loginValue, username: value })
              }
              autocomplete="username"
              className={errors.username !== "" ? "border-red-500" : ""}
            />
            {errors.username && (
              <p className="text-red-500 text-xs">{errors.username}</p>
            )}
          </div>
          <div className={`${getFontClass(language)}`}>
            <label className="flex mb-2 items-center">{t('密碼')}</label>
            <TextInput
              disabled={logging}
              type={showPassword ? "text" : "password"}
              placeholder={t("請輸入密碼")}
              value={loginValue.password}
              onInputChange={(value: any) =>
                setLoginValue({ ...loginValue, password: value })
              }
              rightIcon={eyeIcon()}
              autocomplete={showPassword ? undefined : "current-password"} // 修正條件
              className={errors.password !== "" ? "border-red-500" : ""}
            />
            {errors.password && (
              <p className="text-red-500 text-xs">{errors.password}</p>
            )}
          </div>
          <div className={`${getFontClass(`${language}_m`)} flex flex-col mt-1 `}>
            <button
              onClick={() => navigate("/app/ForgetPassword")}
              className="w-1/2 ml-auto text-red-500 text-right"
              type="button"
              disabled={logging}
            >
              {t('忘記密碼?')}
            </button>
            <Button
              className="button-full active mt-2"
              type="submit"
              disable={logging}
            >
              {t('登入')}
            </Button>
            <Button
              className="button-full secondary mt-2"
              type="button"
              disable={logging}
              onClick={() => navigate("/app/login/register")}
            >
              {t('免費註冊')}
            </Button>
          </div>
        </form>
      </div>
      {shouldHideBackBtn && <BottomTab value={value} setValue={setValue} />}
    </div>
  );
};

export default Login;
